"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";

type User = {
  id: string;
  name: string | null;
  email: string;
  role: "USER" | "ADMIN";
};

export default function LoginPage() {
  const router = useRouter();

  const [mode, setMode] = useState<"login" | "register">("login");
  const [loading, setLoading] = useState(false);
  const [meLoading, setMeLoading] = useState(true);
  const [me, setMe] = useState<User | null>(null);

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState<string | null>(null);

  async function loadMe() {
    try {
      const res = await fetch("/api/auth/me", { cache: "no-store" });
      const json = await res.json();
      setMe(json?.user || null);
    } catch {
      setMe(null);
    } finally {
      setMeLoading(false);
    }
  }

  useEffect(() => {
    loadMe();
  }, []);

  async function submit() {
    setError(null);
    setLoading(true);

    try {
      const endpoint = mode === "login" ? "/api/auth/login" : "/api/auth/register";
      const payload =
        mode === "login"
          ? { email, password }
          : { name: name.trim() || null, email, password };

      const res = await fetch(endpoint, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      });

      const json = await res.json();
      if (!res.ok || !json?.success) {
        throw new Error(json?.error || "Falha na autenticação.");
      }

      await loadMe();
      router.push("/");
      router.refresh();
    } catch (e: any) {
      setError(e.message || "Erro inesperado.");
    } finally {
      setLoading(false);
    }
  }

  if (meLoading) {
    return (
      <div className="fixed inset-0 flex items-center justify-center bg-[#0D0D0D]">
        <span className="text-sm text-gray-400">Carregando…</span>
      </div>
    );
  }

  return (
    <div className="fixed inset-0 bg-[#0D0D0D]">
      <div className="flex min-h-screen items-center justify-center px-4">
        <div className="w-full max-w-[420px]">
          <div className="rounded-2xl border border-[#1A1A1A] bg-[#0F0F0F] p-6 shadow-xl">
            <h1 className="text-2xl font-semibold text-white">
              {mode === "login" ? "Entrar" : "Criar conta"}
            </h1>

            <p className="mt-1 text-sm text-gray-400">
              {mode === "login"
                ? "Acesse sua conta para continuar estudando."
                : "Crie sua conta em menos de 1 minuto."}
            </p>

            <div className="mt-6 space-y-4">
              {mode === "register" && (
                <div>
                  <label className="text-xs text-gray-400">Nome</label>
                  <input
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    className="mt-1 w-full rounded-xl bg-[#0D0D0D] border border-[#2A2A2A] px-3 py-2 text-sm text-white"
                  />
                </div>
              )}

              <div>
                <label className="text-xs text-gray-400">Email</label>
                <input
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  className="mt-1 w-full rounded-xl bg-[#0D0D0D] border border-[#2A2A2A] px-3 py-2 text-sm text-white"
                />
              </div>

              <div>
                <label className="text-xs text-gray-400">Senha</label>
                <input
                  type="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  className="mt-1 w-full rounded-xl bg-[#0D0D0D] border border-[#2A2A2A] px-3 py-2 text-sm text-white"
                />
              </div>

              {mode === "login" && (
                <button
                  onClick={() => router.push("/forgot-password")}
                  className="text-sm text-[#00FF8B] hover:underline"
                  type="button"
                >
                  Esqueci a senha
                </button>
              )}

              {error && (
                <div className="rounded-xl border border-red-500/30 bg-red-500/10 px-3 py-2 text-sm text-red-200">
                  {error}
                </div>
              )}

              <button
                onClick={submit}
                disabled={loading}
                className="w-full rounded-xl bg-[#00FF8B] text-black font-semibold py-2"
              >
                {loading ? "Enviando..." : mode === "login" ? "Entrar" : "Criar conta"}
              </button>

              <button
                onClick={() => setMode(mode === "login" ? "register" : "login")}
                className="w-full rounded-xl border border-[#2A2A2A] text-gray-200 py-2"
              >
                {mode === "login" ? "Criar conta" : "Já tenho conta"}
              </button>
            </div>

            <p className="mt-6 text-center text-xs text-gray-500">
              Ao continuar, você concorda com o uso educacional do app.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}
